<template>
  <div class="register">
    <van-nav-bar title="注册" left-text="登录" right-text="关闭" @click-left="goPage('Login')" @click-right="goMain('Menu')"/>
    <div class="logo">
      <img class="auto-img" src="../assets/images/logo.png" alt />
    </div>

    <van-form @submit="register">
      
      <van-field
        v-model="userInfo.nickName"
        type="text"
        label="昵称"
        placeholder="昵称汉字字母数字组合1-10位"
        left-icon="contact"
        maxlength="8"
      />

      <van-field
        v-model="userInfo.phone"
        type="text"
        label="手机号"
        placeholder="请输入11位的手机号"
        left-icon="phone-o"
        maxlength="11"
      >
       
        <template v-slot:right-icon>
          <i class="fa fa-camera-retro fa-lg"></i>
        </template>
      </van-field>

      <van-field
        v-model="userInfo.password"
        :type="isSee ? 'text':'password'"
        label="密码"
        placeholder="首字母为英文16位数字字母组合"
        left-icon="lock"
        :right-icon="isSee ? 'eye-o':'closed-eye'"
        @click-right-icon="seePassword"
        maxlength="16"
      />
      <div class="register-btn">
        <van-button round block type="info" native-type="submit" color="rgb(249,187,50)">立即注册</van-button>
      </div>
    </van-form>
  </div>
</template>

<script>

import validForm from "../assets/js/formTest";

import '../assets/css/register.less'


export default {
  data() {
    return {
      
      userInfo: {
        nickName: "",
        phone: "",
        password: ""
      },
      
      isSee: false
    };
  },
  methods: {
    //跳转登录
    goPage(name){
      this.$router.push({name});
    },
    goMain(name){
      this.$router.push({name});
    },
    // 3.组件的点击事件
    seePassword() {
      this.isSee = !this.isSee;
    },
    //4.注册
    register() {
      
      if (validForm.validUser(this.userInfo)) {
       
        let data = Object.assign({}, this.userInfo);
       
        data.appkey = this.appkey;
        
        this.$toast.loading({
          message: "加载中...",
          forbidClick: true,
          duration:0
        });

        
        this.axios({
          method: "POST",
          
          url: "/register",
          
          data 
        })
          .then(result => {
            
            //关闭toast提示
            this.$toast.clear();
            //跳转到登录界面
            if(result.data.code ==100){
              this.$router.push({name:'Login'});
            }else{
              this.$toast(result.data.msg);//否则传递信息msg
            }

          })
          .catch(err => {
            
             //关闭toast提示
            this.$toast.clear();
          });
      }
    }
  }
};
</script>
<style lang="less" scoped>

.van-nav-bar {
  background-color:rgb(249,187,50);
  color: #f0f0f0;
}
/deep/ .van-nav-bar__text {
    color: #f0f0f0;
}
</style>

